<template><section><h1>描述</h1>
<p>提供了简单的选择器组件，比起三方组件的提升是提供了选项框覆盖插件功能，集成了scroll组件避免页面插件闪烁</p>
<h1>效果展示</h1>
<template>
<div style="background:#1c3053;height: 60px;padding: 30px;">
  <bs-select :options="options" :scrollOptions="scrollOptions" v-model="v" style="width:200px;">
  </bs-select>
  {{v}}
</div>
</template>

<h1>代码示例</h1>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"background:#1c3053;height: 60px;padding: 30px;"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">bs-select</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"options"</span> <span class="hljs-attr">:scrollOptions</span>=<span class="hljs-string">"scrollOptions"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"v"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:200px;"</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">bs-select</span>&gt;</span>
  {{v}}
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">const</span> options = []
    <span class="hljs-keyword">let</span> i = <span class="hljs-number">22</span>
    <span class="hljs-keyword">while</span> (i--) {
      options.push({
        <span class="hljs-attr">label</span>: i,
        <span class="hljs-attr">value</span>: i
      })
    }
    <span class="hljs-keyword">return</span> {
      options,
      <span class="hljs-attr">scrollOptions</span>: {
        <span class="hljs-attr">color</span>: <span class="hljs-string">'rgba(0,0,0,.75)'</span>,
        <span class="hljs-attr">width</span>: <span class="hljs-string">'8px'</span>,
        <span class="hljs-attr">background</span>: <span class="hljs-string">'rgba(0,0,0,.15)'</span>
      },
      <span class="hljs-attr">v</span>: <span class="hljs-string">''</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h1>props</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>属性名称</th>
<th>描述</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>options</td>
<td>数据对象</td>
<td>Array[{value, label}]</td>
<td>必须</td>
</tr>
<tr>
<td>value</td>
<td>值</td>
<td>String,Number</td>
<td>必须</td>
</tr>
<tr>
<td>scrollNum</td>
<td>大于此数量的选项出滚动条</td>
<td>Number</td>
<td>4</td>
</tr>
<tr>
<td>scrollOptions</td>
<td>滚动条组件样式定义(<a href="#/bs-scroll">详见</a>)</td>
<td>Object</td>
<td>{}</td>
</tr>
</tbody>
</table>
<h1>events</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>事件名称</th>
<th>描述</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>input</td>
<td>选中值</td>
<td>value</td>
</tr>
</tbody>
</table>
<h1>样式定义</h1>
<p><strong>样式覆盖可能需要更高的权重覆盖， 如div.bs-slt-tog</strong></p>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>类名</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>.bs-slt-tog</td>
<td>按钮框</td>
</tr>
<tr>
<td>.bs-slt-tog.open</td>
<td>按钮框展开</td>
</tr>
<tr>
<td>.bs-slt-tog:hover</td>
<td>按钮框鼠标悬浮</td>
</tr>
<tr>
<td>.bs-slt-drp</td>
<td>选项框容器</td>
</tr>
<tr>
<td>.bs-slt-item</td>
<td>单个选项</td>
</tr>
<tr>
<td>.bs-slt-item:hover</td>
<td>选项鼠标悬浮</td>
</tr>
<tr>
<td>.bs-slt-item.selected</td>
<td>已选中选项</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    const options = [
      {
        value: '123',
        label: 'llllllll嘻嘻lllllllllllllllllllllllllllllllllllllllllllllllllll'
      }
    ]
    let i = 22
    while (i--) {
      options.push({
        label: i,
        value: i
      })
    }
    return {
      options,
      scrollOptions: {
        color: 'rgba(0,0,0,.75)',
        width: '8px',
        background: 'rgba(0,0,0,.15)'
      },
      v: ''
    }
  }
}
</script>


